import React, { useState, useEffect, useContext } from 'react';
import { TextField, Button, Typography, Paper, Select, MenuItem, FormControl, InputLabel } from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import FileBase from 'react-file-base64';
import {curricularAspects,
  teachingLearningAndEvaluation,
  researchInnovationAndExtension,
  infrastructureAndLearningResources,
  studentSupportAndProgression,
  governanceLeadershipAndManagement,
  institutionalValuesAndBestPractices} from '../../constants/subCategories';
import { mainCat } from '../../constants/mainCategoreis';  
import useStyles from './styles';
import { createPost, updatePost } from '../../actions/posts';
import { currentUser } from '../../context/context';


const Form = ({ currentId, setCurrentId }) => {
  const {user} = useContext(currentUser);
  console.log(user);
  const [postData, setPostData] = useState({ name: user.name, title: '', description: '', additionalNotes: '', selectedFile: '' , mainCategory: '', subCategory: '', email: user.email});
  const post = useSelector((state) => (currentId ? state.posts.find((description) => description._id === currentId) : null));
  const dispatch = useDispatch();
  const classes = useStyles();

  useEffect(() => {
    if (post) {
      setPostData(post);
      console.log(post);
    }
  }, [post,postData]);

  const clear = () => {
    setCurrentId(0);
    setPostData({ name: user.name, title: '', description: '', additionalNotes: '', selectedFile: '' , mainCategory: '', subCategory: '', email: user.email});
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (currentId === 0) {
      dispatch(createPost(postData));
      clear();
    } else {
      dispatch(updatePost(currentId, postData));
      clear();
    }
  };
  const selCat = postData.mainCategory === 'Curricular Aspects' ? curricularAspects : 
  postData.mainCategory === 'Teaching Learning and Evaluation'? teachingLearningAndEvaluation:
  postData.mainCategory === 'Research, Innovation and Extension'? researchInnovationAndExtension:
  postData.mainCategory === 'Infrastructure and Learning Resources'? infrastructureAndLearningResources:
  postData.mainCategory === 'Student Support and Progression'? studentSupportAndProgression:
  postData.mainCategory === 'Governance, Leadership and Management'? governanceLeadershipAndManagement:
  postData.mainCategory === 'Institutional Values and Best Practices'? institutionalValuesAndBestPractices:[{"type":"Select a main category"}];

  return (
    <Paper className={classes.paper}>
      <form autoComplete="off" noValidate className={`${classes.root} ${classes.form}`} onSubmit={handleSubmit}>
        <Typography variant="h6">{currentId ? `Editing "${post.title}"` : 'Enter the data to be entered'}</Typography>
        <TextField disabled={true} name="Name" variant="outlined" label="Name" fullWidth value={postData.name} onChange={(e) => setPostData({ ...postData, name: e.target.value })} />
        <TextField disabled={true} name="Email" variant="outlined" label="Email" fullWidth value={postData.email} onChange={(e) => setPostData({ ...postData, email: e.target.value })} />
        <TextField name="title" variant="outlined" label="Title" fullWidth value={postData.title} onChange={(e) => setPostData({ ...postData, title: e.target.value })} />
        <TextField name="description" variant="outlined" label="Description" fullWidth multiline rows={4} value={postData.description} onChange={(e) => setPostData({ ...postData, description: e.target.value })} />
        <TextField name="additionalNotes" variant="outlined" label="Additional Notes" fullWidth value={postData.additionalNotes} onChange={(e) => setPostData({ ...postData, additionalNotes: e.target.value })} />
        <FormControl fullWidth>
          <InputLabel>Main Category</InputLabel>  
          <Select value={postData.mainCategory} onChange={(e) => setPostData({ ...postData, mainCategory: e.target.value })}>
              {mainCat.map((c) => <MenuItem key={c.type} value={c.type}>{c.type}</MenuItem>)}
          </Select>
        </FormControl>
        <FormControl fullWidth>
          <InputLabel>Sub Category</InputLabel>  
          <Select value={postData.subCategory} onChange={(e) => setPostData({ ...postData, subCategory: e.target.value })}>
              {selCat.map((c) => <MenuItem key={c.type} value={c.type}>{c.type}</MenuItem>)}
          </Select>
        </FormControl>
        <div className={classes.fileInput}><FileBase type="file" multiple={false} onDone={({ base64 }) => setPostData({ ...postData, selectedFile: base64 })} /></div>
        <Button className={classes.buttonSubmit} variant="contained" color="primary" size="large" type="submit" fullWidth>Submit</Button>
        <Button variant="contained" color="secondary" size="small" onClick={clear} fullWidth>Clear</Button>
      </form>
    </Paper>
  );
};

export default Form;
