import axios from 'axios';

const url = 'http://localhost:5000/posts';

const user_url = 'http://localhost:5000/users';

export const getUser = (email, password) => {
    return axios.post(`${user_url}/login`, { email: email, password: password });
};
export const getUsers = () => {axios.get(`${user_url}`)};
export const createUser = (name, email, password) => {axios.post(`${user_url}/register`, { name: name, email: email, password: password })};
export const deleteUser = (id) => {axios.delete(`${user_url}/${id}`)};
export const updateUser = (id, name, email, password) => {axios.put(`${user_url}/${id}`, { name: name, email: email, password: password })};



export const getUserPosts = (email) => axios.post(url, { email: email });
export const fetchPosts = () => axios.get(url);
export const createPost = (newPost) => axios.post(`${url}/create`, newPost);
export const updatePost = (id, updatedPost) => axios.patch(`${url}/${id}`, updatedPost);
export const deletePost = (id) => axios.delete(`${url}/${id}`);
