import React , {useContext, useEffect, useState} from 'react'
import { Container, AppBar, Typography, Grow, Grid } from '@material-ui/core';
import Posts from '../Posts/Posts';
import Form from '../Form/Form';
import memories from '../../images/memories.png';
import useStyles from './styles';
import { useDispatch } from 'react-redux';
import { currentUser } from '../../context/context';
import {useNavigate} from 'react-router-dom';
import * as api from '../../api/index.js';
import { FETCH_ALL} from '../../constants/actionTypes';

export default () => {
    const navigate= useNavigate();
    const {user} = useContext(currentUser);
    useEffect(() => {
      if(user.email === 'null'){
        navigate('/');
      }
    }
    ,[user, navigate ]);
    const [currentId, setCurrentId] = useState(0);
    const dispatch = useDispatch();
    const classes = useStyles();
    async function getPostsHandler(){
      try{
        const {data} = await api.getUserPosts(user.email);
        dispatch({type: FETCH_ALL, payload: data});
      }
      catch(err){
        console.log(err);
      }
    }
    useEffect(() => {
      getPostsHandler();
    }, [currentId, dispatch]);
  
    return (
        <>
        <Container maxWidth="lg">
          <AppBar className={classes.appBar} position="static" color="inherit">
            <Typography className={classes.heading} variant="h2" align="center">Data Collection-DTU</Typography>
            <img className={classes.image} src={memories} alt="icon" height="60" />
          </AppBar>
          <AppBar className={classes.appBar} position="static" color="inherit">
            <Typography style={{color:'Black'}} className={classes.heading} variant="h5" align="center">Logged In as <b style={{color:'grey'}}>{user.name}</b></Typography>
            
          </AppBar>
          <Grow in>
            <Container>
              <Grid container justify="space-between" alignItems="stretch" spacing={3}>
                <Grid item xs={12} sm={7}>
                  <Posts setCurrentId={setCurrentId} />
                </Grid>
                <Grid item xs={12} sm={4}>
                  <Form currentId={currentId} setCurrentId={setCurrentId} />
                </Grid>
              </Grid>
            </Container>
          </Grow>
        </Container>
        </>
    )
}
