import React, {useState, useEffect, useContext} from 'react'
import './styles.css'
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import LockOpenIcon from "@material-ui/icons/LockOpen";
import axios from 'axios'
import { useNavigate } from "react-router-dom";
import { currentUser } from '../../context/context';

export default () => {
    const {user, dispatch} = useContext(currentUser);
    const navigate = useNavigate();
    useEffect(()=>{
        if(user.email!=='null'){
            navigate('/admin');
        }
    },[user,navigate])
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const signIn = async (e) =>{
      e.preventDefault();

      console.log(email, password);
      try{
        const response = await axios.post('http://localhost:5000/admin/login', {
          email,
          password
        })
        console.log(response)
        if(response.status === 200){
          alert('Login Successful');
          const data={email: response.data.email, name:response.data.name, token: response.data.token};
          console.log(data);
          dispatch({type: 'LOGIN', payload: data});
        }
        else if(response.status === 202){
          alert('Wrong Password');
        }
        else{
          alert('User not found');
        }
      }catch(err){
        console.log(err)
      }
      return false;
    }
    return (
        <div className="LoginSignUpContainer">
            <div className="LoginSignUpBox">
              <div>
                <div className="login_signUp_toggle">
                  <p >ADMIN LOGIN</p>
                </div>
                <button style={{backgroundColor: 'black'}}></button>
              </div>
              <div className="loginForm" >
                <div className="loginEmail">
                <MailOutlineIcon />
                  <input
                    type="email"
                    placeholder="Email"
                    required
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </div>
                <div className="loginPassword">
                <LockOpenIcon />
                  <input
                    type="password"
                    placeholder="Password"
                    required
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </div>
                <button style={{backgroundColor: 'black'}} className="loginBtn" onClick={signIn}>Login</button>
              </div>
              
            </div>
          </div>
    )
}
